@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;0,700;0,900;1,400;1,500;1,700&display=swap);

.feature{
    margin: 15px 2% ;
    max-width: 100%;
    background-color: #EBEEEF;
}

.feacture_heading{
    display: flex;
    justify-content: space-between;
}

.feacture_heading h2{
    margin: 15px;
    font-weight: 400;
    font-family: 'Roboto', sans-serif;
}

.feacture_heading p{
    margin: 15px;
    font-weight: 800;
    font-family: 'Roboto', sans-serif;
    border-bottom: 2px solid black;
}

.feacture_heading p:hover{
    border-bottom: none;
    cursor: pointer;
}


.items_div{
    padding: 20px;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-around;
    flex-wrap: wrap;
}

.item_main{
    text-decoration: none;
    background-color: #fff;
    /* max-width: 90%; */
    padding: 10px 10px 10px 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* align-items: center; */
    border: 1px solid #b6b2b2;
    border-radius: 10px;
    margin: 10px 0;
}

.item_image img{
    width: 280px;
    height: 200px;
}

.item_price h5{
    font-size: 22px ;
    color: #002F34;
    margin: 3px 0;
}

.item_name h5{
    font-size: 16px;
    font-weight: 200;
    margin: 5px 0;
    text-transform: capitalize;
    color: #002F34;
}
/* float: right; */

.place_time{
    margin: 5px 0;
    display: flex;
    justify-content: space-between;
    text-transform: capitalize;
}

.item_location h5{
    font-size: 12px;
    font-weight: 200;
    text-transform: capitalize;
    color: #002F34;
}

.item_date h5{
    font-size: 12px;
    font-weight: 200;
    color: #002F34;
}



/* fresh category */

.fresh{
    margin: 15px 1% ;
    max-width: 100%;
    margin-bottom: 50px;
}

.fresh_heading h2{
    margin: 15px;
    font-weight: 400;
    font-family: 'Roboto', sans-serif;
}


.ad_link{
    text-decoration: none;
}

/* footer banner */

.footer_banner{
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #F7F8F9;
    padding: 0 55px;
}

.footerBannerText{
    margin-right: 75px;
    border-right: 2px solid #beb8b8;
}

.footerBannerText h1{
    color: #002F34;
    text-transform: uppercase;
    
}

.footerBannerText h2{
    color: #002F34;
    font-weight: 200;
    margin-right: 45px;
    margin-top: 20px;
    margin-bottom: 20px;
}

.storeImg{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.footerStoreImg h4{
    margin-bottom: 18px;
    font-weight: 500;
    font-size: 14px;
}

.storeImg img{
    width: 95%;
    cursor: pointer;
}

@media screen and (max-width: 900px) {
    .footerBannerText{
        border-right: none;
    }
    .footerBannerText{
        display: none;
    }
    .footerStoreImg{
        display: none;
    }


}

/* footer style start */
.footer_category{
    border-top: 1px solid #c0c5c7;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding: 30px 25px;
    background-color: #EBEEEF;
}

.footer_category h1{
    font-size: 14px;
    color: #002F34;
    margin-bottom: 10px;
}

.footer_category p{
    font-size: 13px;
    color: #002F34;
    margin-bottom: 3px;
    font-weight: 200;
}

.socialIcons{
    display: flex;
    flex-wrap: wrap;
}

.footerSocial{
    margin: 10px 0px;
    display: flex;
    justify-content: start;
    flex-wrap: wrap;
}

.footerSocial img{
    margin: 20px 3px;
    width: 25%;
    cursor: pointer;
}

.footer{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    background-color: #002F34;
    color: white;
    padding: 20px 100px;
}

.countries{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.copyRight{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.footer h3{
    font-size: 14px;
}

.footer h4{
    font-size: 14px;
    font-weight: 200;
    margin-left: 5px;
    cursor: pointer;
}
/* Header style start */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

.header {
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  left: 0px;
  right: 0px;
  height: 70px;
  /* z-index: 10; */
  background-color: #f1f1f1;
  box-shadow: 0px 2px 2px #dadada;
  display: flex;
  align-items: center;
  /* padding: 10px; */
  justify-content: space-around !important;
  flex-wrap: wrap;
}

.logo {
  width: 30px;
  /* margin-left: 10px; */
}
.logo img {
  width: 50px;
  /* margin-left: 10px; */
}

.location {
  border: 2px solid black;
  padding: 10px;
  border-radius: 7px;
  background-color: #fff;
  justify-content: space-around;
  width: 15%;
  margin-left: 0;
}

.icon_search {
  display: flex;
  align-items: center;
  /* width: 100px; */
}

.citySrchIcon {
  width: 35px !important;
  height: 30px !important;
}

.citySearch {
  outline: none;
  border: none;
  font-size: 20px;
  margin-left: 10px;
  margin-right: 30px;
  /* margin-top: -52px !important; */
  width: 100%;
  color: #8d8989;
  /* vertical-align: text-center; */
}

.location button {
  outline: none;
  border: none;
  background-color: transparent;
}

.cityDropDown {
  width: 15px;
  height: 15px;
}

.searchItem {
  border: 2px solid black;
  /* padding: 10px; */
  border-radius: 7px;
  background-color: #fff;
  margin: 10px;
  /* height: 60px; */
  display: flex;
  align-items: center;
}

.searchItem input {
  outline: none;
  border: none;
  font-size: 20px;
  margin: 13px;
  width: 500px;
  /* height: 10px; */
  color: #8d8989;
}

.ItemSearchIcon {
  background-color: #002f34;
  color: white !important;
  float: right;
  width: 45px !important;
  height: 50px !important;
  /* padding: 15px; */
}

.login button {
  outline: none;
  border: none;
  border-bottom: 2px solid black;
  font-weight: 600;
  font-size: 20px;
  font-family: "Roboto", sans-serif;
}
.login button:hover {
  border: none;
  cursor: pointer;
}

.signUp {
  width: 100px;
  /* margin-right: 5px; */
}

.signUp button {
  border: none;
  background: transparent;
  width: 55px;
  display: flex;
  justify-content: center;
  outline: none;
  /* font-weight: 300; */
}
.signUp button:hover {
  cursor: pointer;
}
.signUpStyle {
  padding: 5px;
  background-color: #fff;
  border-radius: 50px;
  border-top: 6px solid #23e5db;
  border-left: 6px solid #ffce32;
  border-right: 6px solid #3a77ff;
  border-bottom: 6px solid #ffce32;
  width: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.sell {
  font-size: 20px;
  font-weight: 600;
  display: inline-block;
}

/* Header style end */

/* category style start */



.main_category {
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-transform: capitalize;
  margin: 10px 0;
  font-family: "Roboto", sans-serif;
}

.main_category div:hover{ 
  cursor: pointer;
}

.allCategory {
  display: flex;
  /* justify-content: space-around; */
  align-items: center;
  text-transform: uppercase;
  font-weight: 600;
  flex-wrap: wrap;
  cursor: pointer;
}



.banner img {
  width: 100%;
}


.goToHome {
  background-color: transparent;
  opacity: 0.5;
  z-index: 5;
  width: 100%;
  height: 100vw;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  position: fixed;
}


.comCategory {
  z-index: 20;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  grid-gap: 0px 0px;
  gap: 0px 0px;
  width: 100%;
  margin: auto;
  position: absolute;
  background-color: #fff;
  /* align-items: center; */
  top : 110px;
  padding: 15px 15px;
  /* left: 10px; */
  
}

.comCategory h5 {
  font-size: 14px;
  /* margin-right: 200px; */
}

.comCategory h6 {
  font-size: 12px;
  line-height: 20px;
  /* margin-right: 200px; */
  font-weight: 400;
}

.comCategory h6:hover {
  color: #00A4B8;
  cursor: pointer;
}



.main_login {
  background-color: black;
  opacity: 0.5;
  z-index: 11;
  width: 100%;
  height: 100vw;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  position: fixed;
}

.UserInfo{
  display: flex;
  justify-content:center;
  align-items:center;
}

.UserInfo:hover{
  cursor: pointer;
}

.ChatIcon, .NotificationIcon{
  color: #002F34;
}


